.app-content {
    position: relative;
    -webkit-transition: margin-left .2s ease-in;
    -o-transition: margin-left .2s ease-in;
    transition: margin-left .2s ease-in;
    height: 90%;
    margin-left: 0;
}

.app-content-actived {
    margin-left: 20%;
}

.app-content .toggle-siderbar-button {
    position: absolute;
    background: white;
    padding: 15px;
    left: 5px;
    top: 5px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    cursor: pointer;
    -ms-flex-pack: center;
        justify-content: center;
    border-radius: 50%;
    outline: none;
}

.toggle-siderbar-button span {
    position: absolute;
    bottom: 8px;
}

.content-block {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-direction: column;
        flex-direction: column;
    color: #fff;
    overflow: hidden;
    -webkit-transition: height .2s ease-in;
    -o-transition: height .2s ease-in;
    transition: height .2s ease-in;
}

.content-block-subTitle {
    position: absolute;
    margin-top: 25px;
    opacity: 0;
    -webkit-transition: 0.1s ease-out;
    -o-transition: 0.1s ease-out;
    transition: 0.1s ease-out;
    font-weight: 300;
}

.content-block .subTitle-actived {
    opacity: 1;
    -webkit-transition: 0.6s ease-in;
    -o-transition: 0.6s ease-in;
    transition: 0.6s ease-in;
}



