.app-sidebar {
    height: 100%;
    width: 20%;
    background-color: #2b3636;
    color: #fff;
    position: fixed;
    left: 0;
    text-align: center;
    -webkit-transition: left .2s ease-in;
    -o-transition: left .2s ease-in;
    transition: left .2s ease-in;
}

.app-sidebar-hidden {
    left: -20%;
}


